.header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 100vw;
	height: $gutter-4;

	& > p {
		position: relative;
		justify-content: center;
		padding: $gutter;
		color: $shadow;

		&:before {
			@include absolute(top $gutter);
			width: $gutter-3;
			height: $gutter-2;
		}
	}

	&__back {
		width: 20vw;

		&::before {
			left: $gutter;
			content: url('data:image/svg+xml;base64,');
		}
	}

	&__score {
		width: 20vw;

		&::before {
			left: 50%;
			content: url('data:image/svg+xml;base64,');
		}
		span {
			position: absolute;
			right: 50%;
			color: $cool;
		}
	}

	&__level {
		width: 60vw;
		text-align: center;
		font-weight: 100;

		span {
			font-weight: 300;
		}
	}

	&__volume {
		width: 20vw;

		&::before {
			right: 50%;
			content: url('data:image/svg+xml;base64,');
		}
	}

	&__share {
		position: relative;
		padding: $gutter-1-2 $gutter-2 $gutter-1-2 $gutter-1-2;
		font-size: 0.5rem;
		font-weight: 500;
		text-decoration: none;
		color: $paper;
		background-color: $cool;
		border-radius: $gutter-1-2;
	
		&::before {
		  position: absolute;
		  top: $gutter-1-2;
		  right: $gutter-1-3;
		  max-width: 18px;
		  content: url('data:image/svg+xml;base64,');
		}
	  }
}
